.fieldAgent{
    height: 100%;
    width: 25%;
    box-shadow: 0px 0px 2px 0px rgb(168, 168, 168);
    display: flex;
    flex-direction: column;
    padding: 10px;
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
    /* background-color: rgb(247, 250, 250); */
}

.fieldAgent::-webkit-scrollbar {
    display: none;
}

.center{
    justify-content: center;
    align-items: center;
}

.error{
    color: red;
}

.button{
    /* border: 2px solid red; */
    display: flex;
    justify-content: flex-end;
    padding: 10px;
}

.button button{
    width: 80px;
    height: 30px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
}

.button button:hover{
    cursor: pointer;
}