.Assigning{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid rgb(245, 242, 242);
}

.heading{
    margin-bottom: 30px;
    font-size: larger;
    font-weight: bold;
}

.message{
    margin-bottom: 20px;
}

.AssignForm{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.AssignForm input{
    border: 1px solid black;
    width: 70%;
    padding: 7px;
    margin-bottom: 15px;
    border-radius: 2px;
}

.Assign_button{
    width: 160px;
    padding: 8px;
    background-color: black;
    border: none;
    color: white;
    border-radius: 5px;
}

.Assign_button:hover{
    cursor: pointer;
}