.AgentContainer{
    height: 100%;
    width: 70%;
    box-shadow: 0.5px 0.5px 5px 0px rgb(168, 168, 168);
    padding: 20px;
    display: flex;
    /* border: 1px solid black; */
}

.Agent{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 50%;
    overflow: hidden;
}

.intro{
    display:  flex;
    justify-content: space-between;
    height: 120px;
    /* border: 0.5px solid rgb(226, 222, 222); */
    padding-bottom: 20px;
    height: 25%;
}

.intro table{
    border-collapse: collapse;
    /* height: 100px;
    border: 1px solid red; */
}

.intro td{
    padding: 0px 20px;
    /* border: 2px solid red; */
    font-size: small;
}

.value{
    font-weight: lighter;
}

.intro tr:hover{
    background-color: rgb(228, 225, 225);
}


.work{
    display: flex;
    /* padding-top: 20px; */
    font-size: small;
    height: 75%;
}

.pending{
    width: 50%;
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
}

.done{
    width: 50%;
    display: flex;
    flex-direction: column;
    /* border: 1px solid red; */
}


.heading{
    text-align: center;
}

.content{
    padding: 0px 2px; 
    flex-grow: 1;
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
    /* border: 1px solid red; */
}

.content::-webkit-scrollbar {
    display: none;
}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
}