.Addagent_container{
    /* border: 2px solid black; */
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.submitting{
    color: blue;
}

.submitted{
    color: green;
}

.Add_error{
    color: red;
}

.Addagent{
    /* border: 2px solid red; */
    width: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Addagent p{
    font-size: 1.3rem;
    margin-bottom: 25px;
}

.Addagent input{
    width: 95%;
}

.button_Addagent{
    margin-top: 40px;
    width: 40%;
}

.button_Addagent button{
    width: 100%;
    height: 30px;
    border: none;
    background-color: black;
    border-radius: 5px;
    color: white;
}

.button_Addagent button:hover{
    cursor: pointer;
}